.change-password.ant-row {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
}
.change-password-sticky-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 72px;
  border-top: solid 1px #d4d7d9;
}
.change-password-sticky-footer > div {
  padding-top: 16px;
  padding-right: 16px;
}
@media (min-width: 1025px) {
  .msg {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
  }
}
@media (max-width: 1024px) and (min-width: 601px) {
  .msg {
    position: fixed;
    bottom: 72px;
    left: 0;
    right: 0;
  }
}
@media (max-width: 600px) and (min-width: 361px) {
  .msg {
    position: fixed;
    bottom: 72px;
    left: 0;
    right: 0;
  }
}
@media (max-width: 360px) {
  .msg {
    position: fixed;
    bottom: 72px;
    left: 0;
    right: 0;
  }
}
