@media (min-width: 1025px) {
  #root .header {
    background-color: #32b9d1;
    color: #ffffff;
  }
  #root .header .logo-block {
    height: 72px;
  }
  #root .upper-content {
    padding: 0 248px;
  }
  #root .upper-content {
    background-color: #ffffff;
  }
  #root .computerDeskDoctor {
    position: relative;
    height: 400px;
    z-index: 0;
  }
  #root .computerDeskDoctor .hero_background {
    background-image: url("../../../Assets/images/hero-bg@3x.png");
    background-size: cover;
    z-index: -1;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
  }
  #root .computerDeskDoctor .hero_container {
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 1;
  }
  #root .computerDeskDoctor .hero_container > div > h1 {
    width: 720px;
    font-size: 40px;
  }
  #root .landing-copyright-bottom {
    padding-top: 64px;
    padding-bottom: 16px;
  }
}
